
import { defineComponent } from 'vue';
import { mapGetters } from 'vuex';

export default defineComponent({
	data() {
		return {
			apiUrl: process.env.VUE_APP_BACKEND,
		};
	},
	async mounted() {
		await this.$store.dispatch('getExpertsAction');
	},
	computed: {
		...mapGetters(['getExperts']),
	},
});
